'use strict';

var assign = require('object-assign');
var React = require('react/addons');
var Reflux = require('reflux');
var MapViewActions = require('../actions/mapViewActions');
var FilterActions = require('../actions/filterActions');
var FiltersViewActions = require('../actions/filtersViewActions');
var FiltersViewStore = require('../stores/filtersViewStore');
var CountryActions = require('../actions/countryActions');
var CountrySummaryStore = require('../stores/countrySummaryStore');
var SectorStore = require('../stores/sectorStore');
var StatusStore = require('../stores/statusStore');
var ApprovalYearStore = require('../stores/approvalYearStore');
var BasemapsStore = require('../stores/basemapsStore');
var ThematicLayersStore = require('../stores/thematicLayersStore');
var FilterStore = require('../stores/filterStore');
var ErrorMessage = require('./error.jsx');
var Map = require('./map/map.jsx');  // real maps for basemaps gallery :D
var EsriTileLayer = require('./map/esriTileLayer.jsx');
var api = require('../api.js');


var filters = [
  {
    id: 'country',
    isPrimary: true,
    name: 'Country',
    adbicon: 'globe3'
  },
  {
    id: 'sectors',
    isPrimary: true,
    name: 'Sector',
    glyphicon: 'grain'
  },
  {
    id: 'status',
    isPrimary: true,
    name: 'Status',
    glyphicon: 'ok-circle'
  },
  {
    id: 'approvalYear',
    isPrimary: true,
    name: 'Approval Year',
    glyphicon: 'calendar',
    className: ' small-icon',
  },
  {
    id: 'thematicLayer',
    isPrimary: false,
    name: 'Thematic Layers',
    adbicon: 'layers',
  },
  {
    id: 'basemap',
    isPrimary: false,
    name: 'Map Style',
    adbicon: 'globe1',
  },
  {
    id: 'share',
    isPrimary: false,
    name: 'Share / Download',
    glyphicon: 'link'
  }
];

var FilterNavButton = React.createClass({displayName: "FilterNavButton",
  toggleMe: function(e) {
    if (e) { e.preventDefault(); }  // if this is coming from a click event
    if (this.props.open) {
      FiltersViewActions.close();
    } else {
      FiltersViewActions.open(this.props.id);
    }
  },

  render: function() {
    var buttonClass = 'nav-button';
    if (this.props.isPrimary) {
      buttonClass += ' nav-button-primary';
    }
    if (this.props.open) {
      buttonClass += ' open';
    }
    if (this.props.active) {
      buttonClass += ' active';
    }

    var iconTag;
    if (this.props.glyphicon) {
      iconTag = ( React.createElement("span", {className: 'glyphicon glyphicon-' + this.props.glyphicon, "aria-hidden": "true"}) );
    }
    else if (this.props.adbicon){
      iconTag = ( React.createElement("span", {className: 'adbicon adbicon-' + this.props.adbicon, "aria-hidden": "true"}) );
    }

    var specialClass = '';
    if (this.props.className) {
      specialClass = [' ',this.props.className].join(' ');
    }
    return (
      React.createElement("li", {role: "presentation", className: [buttonClass,this.props.className].join(' ')}, 
        React.createElement("a", {href: "#", onClick: this.toggleMe}, 
          iconTag, 
          this.props.name, 
          this.props.total > 0 && this.props.applied > 0 &&
            React.createElement("small", null, "(", this.props.applied, "/", this.props.total, ")")
          
        )
      )
    );
  }
});


var ListItemMixin = {
  render: function() {
    return (
      React.createElement("li", null, 
        React.createElement("input", {
          id: this.props.id, 
          className: "visuallyhidden", 
          type: "checkbox", 
          checked: this.props.checked, 
          onChange: this.toggle}), 
        React.createElement("label", {
          htmlFor: this.props.id, 
          className: this.props.itemClass || 'circle-check'}, 
          this.props.label || this.props.text
        )
      )
    );
  },
};

var RadioListItem = React.createClass({displayName: "RadioListItem",
  mixins: [ListItemMixin],
  toggle: function(e) {
    e && e.preventDefault();
    if (this.props.checked) {
      FilterActions.clearFilter(this.props.name, this.props.text);
    } else {
      FilterActions.setFilter(this.props.name, this.props.text);
    }
  },
});


var CountryRadioListItem = React.createClass({displayName: "CountryRadioListItem",
  mixins: [ListItemMixin],
  toggle: function(e) {
    e && e.preventDefault();
    if (this.props.checked) {
      CountryActions.clearCountries();
      CountryActions.removeCountry(this.props.countryId);
    } else {
      CountryActions.addCountry(this.props.countryId);
    }
  }
});


var BasemapRadioListItem = React.createClass({displayName: "BasemapRadioListItem",
  mixins: [ListItemMixin],
  toggle: function(e) {
    e && e.preventDefault();
    MapViewActions.changeBasemap(this.props.basemapId);
  }
});


var DownloadListItem = React.createClass({displayName: "DownloadListItem",
  toggle: function(e) {
    e && e.preventDefault();
  },
  render: function() {
    var path="csv/countries/" + this.props.countryId + ".csv";
    return (
      React.createElement("li", null, 
        React.createElement("a", {href: path}, this.props.name)
      )
    );
  },
});


var CloseButton = React.createClass({displayName: "CloseButton",
  clickClose: function(e) {
    e.preventDefault();
    FiltersViewActions.close();
  },
  render: function() {
    return (
      React.createElement("a", {
      className: 'close-filters-body ' + (this.props.className || ''), 
      onClick: this.clickClose, 
      href: "#"}, 
      "×"
    )
    );
  },
});


var DownloadButton = React.createClass({displayName: "DownloadButton",
  clickDownload: function(e) {
    var apiBaseUrl = api.getAPIBase();
    e.preventDefault();
    this.props.countries.map(function(id) {
      window.open(apiBaseUrl + '/csv/countries/' + id + '.csv');
    });
  },
  render: function() {
    return (
      React.createElement("button", {
        onClick: this.clickDownload}, 
        "Download All (", this.props.countries.length, " files)"
      )
    );
  },
})


var ThematicRadioListItem = React.createClass({displayName: "ThematicRadioListItem",
  mixins: [ListItemMixin],
  toggle: function(e) {
    e && e.preventDefault();
    MapViewActions.changeThematic(this.props.thematicId);
  }
});


var FilterBody = React.createClass({displayName: "FilterBody",
  selectAll: function(e) {
    e.preventDefault();
    FilterActions.selectAll(this.props.id);
  },
  selectNone: function(e) {
    e.preventDefault();
    FilterActions.selectNone(this.props.id);
  },
  selectURL: function() {
    var shareInput = this.refs.shareURL.getDOMNode();
    shareInput.select(0, shareInput.value.length);
  },
  componentDidMount: function() {
    CountryActions.load();
  },
  render: function() {
    if (this.props.id === 'country') {
      var countries = this.props.countries.features;
      var selectedCountries = this.props.query.c || [];
      return (
        React.createElement("div", null, 
          React.createElement(CloseButton, {className: "pull-right"}), 
          React.createElement("h3", null, this.props.name), 
          countries.length === 0 ?
            React.createElement("p", null, "Loading countries...") :
            React.createElement("ul", {className: "filter-list compact"}, 
              countries.map(function(country) {
                return React.createElement(CountryRadioListItem, {
                  key: country.id, 
                  id: 'country-choice-' + country.id, 
                  countryId: country.id, 
                  text: country.properties.name, 
                  label: country.properties.name, 
                  name: this.props.id, 
                  checked: selectedCountries.indexOf(country.id) !== -1})
              }, this)
            )
          
        )
      );
    } else if (this.props.id === 'sectors') {
      var apiBaseUrl = api.getAPIBase();

      if(this.props.sectors.length > 0) {
        return (
          React.createElement("div", null, 
            React.createElement(CloseButton, {className: "pull-right"}), 
            React.createElement("h3", null, this.props.name, 
              React.createElement("small", {className: "select-all-none"}, 
                React.createElement("a", {href: "#", onClick: this.selectAll}, "Select all"), 
                React.createElement("a", {href: "#", onClick: this.selectNone}, "None")
              )
            ), 
            React.createElement("ul", {className: "filter-list"}, 
              this.props.sectors.map(function(sector, i) {
                var label = (
                  React.createElement("span", null, 
                    React.createElement("img", {
                      src: apiBaseUrl + 'images/sector/' + sector + '.svg', 
                      alt: 'Sector: ' + sector}), " ", sector, " (", this.props.sectorObject[sector], ")"
                  )
                );
                return React.createElement(RadioListItem, {
                  key: sector, 
                  id: 'sector-choice-' + i, 
                  itemClass: "icon", 
                  text: sector, 
                  label: label, 
                  name: this.props.id, 
                  checked: this.props.filter.sectors.indexOf(sector) > -1});
              }, this)
            )
          )
        );
      } else {
        return (
          React.createElement("div", null, 
            React.createElement(CloseButton, {className: "pull-right"}), 
            React.createElement("h3", null, this.props.name), 
            React.createElement("p", null, " No sector data currently available for selected country.")
          )
        );
      }
    } else if (this.props.id === 'status') {
      if(this.props.status.length > 0) {
        return (
          React.createElement("div", null, 
            React.createElement(CloseButton, {className: "pull-right"}), 
            React.createElement("h3", null, 
              this.props.name
            ), 
            React.createElement("ul", {className: "filter-list"}, 
              this.props.status.map(function(stat, i) {
                return React.createElement(RadioListItem, {
                  key: i, 
                  id: 'status-choice-' + i, 
                  label: stat + ' (' + this.props.statusObject[stat] + ')', 
                  text: stat, 
                  name: this.props.id, 
                  checked: this.props.filter.status.indexOf(stat) > -1});
              }, this)
            )
          )
        );
      } else {
        return (
          React.createElement("div", null, 
            React.createElement(CloseButton, {className: "pull-right"}), 
            React.createElement("h3", null, this.props.name), 
            React.createElement("p", null, " No status data currently available for selected country.")
          )
        );
      }
    } else if (this.props.id === 'approvalYear') {
      if(this.props.approvalYear.length > 0) {
        return (
          React.createElement("div", null, 
            React.createElement(CloseButton, {className: "pull-right"}), 
            React.createElement("h3", null, 
              this.props.name, 
              React.createElement("small", {className: "select-all-none"}, 
                React.createElement("a", {href: "#", onClick: this.selectAll}, "Select all"), 
                React.createElement("a", {href: "#", onClick: this.selectNone}, "None")
              )
            ), 
            React.createElement("ul", {className: "filter-list"}, 
              this.props.approvalYear.map(function(year, i) {
                return React.createElement(RadioListItem, {
                  key: i, 
                  id: 'approval-choice-' + i, 
                  label: year + ' (' + this.props.yearObject[year] + ')', 
                  text: year, 
                  name: this.props.id, 
                  checked: this.props.filter.approvalYear.indexOf(year) > -1});
              }, this)
            )
          )
        );
      } else {
        return (
          React.createElement("div", null, 
            React.createElement(CloseButton, {className: "pull-right"}), 
            React.createElement("h3", null, this.props.name), 
            React.createElement("p", null, " No approval year data currently available for selected country.")
          )
        );
      }
    } else if (this.props.id === 'thematicLayer') {
      var thematicFilterList = this.props.thematicLayers.map(function(thematic) {
        var label = (
          React.createElement("span", {class: "thematic-choice"}, 
            thematic.name, 
            React.createElement("p", null, 
            thematic.description
            )
          )
        );
        var selectedCountries = this.props.query.c || [];
        if (selectedCountries.indexOf(thematic.country) !== -1) {
          return (
              React.createElement(ThematicRadioListItem, {
              key: thematic.id, 
              id: 'thematic-choice-' + thematic.id, 
              itemClass: "map-select circle-check", 
              thematicId: thematic.id, 
              text: thematic.name, 
              label: label, 
              name: this.props.id, 
              checked: thematic.selected})
          );
        }
        else {
          return null;
        }
      }, this);

      var isNull = function(n) { return n === null;};
      if (thematicFilterList.every(isNull)) {
        thematicFilterList = (
          React.createElement("li", null, 
            React.createElement("label", null, 
            React.createElement("span", null, 
              React.createElement("span", {class: "thematic-choice"}, 
                "No thematic layers available"
              ), 
              React.createElement("p", null, "for the current selected countries. Add another country.")
            )
            )
          )
        );
      }

      return (
        React.createElement("div", null, 
          React.createElement(CloseButton, {className: "pull-right"}), 
          React.createElement("h3", null, this.props.name), 
          React.createElement("ul", {className: "filter-list compact thematic-layers"}, 
            thematicFilterList
          )
        )
      );
    } else if (this.props.id === 'basemap') {
      return (
        React.createElement("div", null, 
          React.createElement(CloseButton, {className: "pull-right"}), 
          React.createElement("h3", null, this.props.name), 
          React.createElement("ul", {className: "filter-list basemaps"}, 
            this.props.basemaps.map(function(basemap) {
              var label = (
                React.createElement("span", null, 
                  basemap.name, 
                  React.createElement(Map, {
                    attributionControl: false, 
                    zoomControl: false, 
                    doubleClickZoom: false, 
                    scrollWheelZoom: false, 
                    dragging: false, 
                    className: "basemap-box", 
                    ref: "mapThumb"}, 
                    React.createElement(EsriTileLayer, {theme: basemap.id})
                  )
                )
              );
              return React.createElement(BasemapRadioListItem, {
                key: basemap.id, 
                id: 'basemap-choice-' + basemap.id, 
                itemClass: "map-select", 
                basemapId: basemap.id, 
                text: basemap.name, 
                label: label, 
                name: this.props.id, 
                checked: basemap.selected});
            }, this)
          ), 
          React.createElement("div", {className: "disclaimer"}, 
            "DISCLAIMER: Country borders or names do not necessarily reflect the Asian Development Bank official position. This map is for illustrative purposes and does not imply the expression of any opinion on the part of the Asian Development Bank, concerning the legal status of any country or territory or concerning the delimitation of frontiers or boundaries." 
          )
        )
      );
    } else if (this.props.id === 'share') {
      var selectedCountries = this.props.query.c || [];
      var countries = this.props.countries.features;
      var apiBaseUrl = api.getAPIBase();
      return (
        React.createElement("div", null, 
          React.createElement(CloseButton, {className: "pull-right"}), 
          React.createElement("h3", {className: "download"}, "Share"), 
          React.createElement("label", {htmlFor: "shareURL"}, "Share Link:"), 
          React.createElement("input", {
            id: "shareURL", 
            className: "download", 
            ref: "shareURL", 
            type: "text", 
            value: window.location, 
            onChange: function(e) { e.preventDefault(); }, 
            onFocus: this.selectURL}), 
          React.createElement("div", {className: "spacer"}), 
          React.createElement("h3", {className: "download"}, "Download CSV"), 
          React.createElement("label", {className: "download"}, "Download data for selected countries:"), 
          React.createElement("ul", {className: "filter-list compact download-csv"}, 
            countries.map(function(country) {
              if(selectedCountries.indexOf(country.id) !== -1) {
                return React.createElement(DownloadListItem, {
                  name: country.properties.name, 
                  countryId: country.id})
              }
            }, this)
          ), 
          React.createElement("div", {className: "download-icon"}, 
            React.createElement("img", {className: "download", src: apiBaseUrl + 'images/download.svg'}), 
            React.createElement(DownloadButton, {countries: selectedCountries})
          )
        )
      );
    } else {
      console.error('unexpected filter id', this.props.id);
    }
  },
});


module.exports = React.createClass({displayName: "exports",

  mixins: [
    Reflux.connect(FiltersViewStore, 'fv'),
    Reflux.connect(CountrySummaryStore, 'countries'),
    Reflux.connect(SectorStore, 'sectors'),
    Reflux.connect(StatusStore, 'status'),
    Reflux.connect(ApprovalYearStore, 'approvalYear'),
    Reflux.connect(FilterStore, 'filter'),
    Reflux.connect(ThematicLayersStore, 'thematicLayers'),
    Reflux.connect(BasemapsStore, 'basemaps'),
    React.addons.PureRenderMixin,
  ],

  toggleMinimize: function(e) {
    if (e) { e.preventDefault() };  // if this is coming from a click event

    if (this.state.fv.isMinimized) {
      FiltersViewActions.unminimize();
    } else {
      FiltersViewActions.minimize();
    }
  },

  componentDidUpdate: function() {
    if (this.props.notifyWidthChange) {
      var newWidth = this.refs.sidebar.getDOMNode().clientWidth;
      this.props.notifyWidthChange(newWidth);
    }

  },

  makeButton: function(filter) {
    var isOpen = filter.id === this.state.fv.currentlyOpen;
    var isActive = (this.state.fv.applied.indexOf(filter.id) !== -1) ||
      (filter.id === 'country' &&
        this.props.query.c && this.props.query.c.length > 1);

    // include total option counts and applied counts if possible
    var total;
    var applied;
    if (this.state[filter.id] !== undefined) {
      total = Object.keys(this.state[filter.id]).length;
      applied = this.state.filter[filter.id].length;
    }

    return (
      React.createElement(FilterNavButton, React.__spread({
        key: filter.id}, 
        filter, 
        {applied: applied, 
        total: total, 
        active: isActive, 
        open: isOpen}))
    );
  },

  render: function() {

    var isMinimizedClass = '';
    if (this.state.fv.isMinimized) {
      isMinimizedClass = ' minimize';
    }

    var primaryButtons = filters
      .filter(function(button) {
        return button.isPrimary;
      })
      .map(this.makeButton);

    var secondaryButtons = filters
      .filter(function(button) {
        return !button.isPrimary;
      })
      .map(this.makeButton);

    var openFilter;
    if (this.state.fv.currentlyOpen) {
      filters.forEach(function(filter) {
        if (filter.id === this.state.fv.currentlyOpen) {
          openFilter = (
            React.createElement("div", {className: "filters-body"}, 
              React.createElement(FilterBody, React.__spread({}, 
                filter, 
                {countries: this.state.countries, 
                sectors: (Object.keys(this.state.sectors)).sort(), 
                sectorObject: this.state.sectors, 
                status: Object.keys(this.state.status), 
                statusObject: this.state.status, 
                approvalYear: Object.keys(this.state.approvalYear), 
                yearObject: this.state.approvalYear, 
                filter: this.state.filter, 
                thematicLayers: this.state.thematicLayers, 
                basemaps: this.state.basemaps, 
                query: this.props.query}))
            )
          );
        }
      }, this);
    }

    var error = (
        React.createElement(ErrorMessage, {
          header: "No Results", 
          text: "No data available for selected filter combination."}
         )
    );


    return (
      React.createElement("div", {className: "filters"}, 

        React.createElement("div", {className: "filters-nav" + isMinimizedClass, ref: "sidebar"}, 
          error, 

          React.createElement("a", {href: "#", onClick: this.toggleMinimize}, 
            React.createElement("h3", {className: "filters-title"}, 
                "Map Controls"
            )
          ), 

          React.createElement("ul", {className: "nav nav-stacked filters-nav-buttons primary"}, 
            primaryButtons
          ), 

          React.createElement("ul", {className: "nav nav-stacked filters-nav-buttons secondary"}, 
            secondaryButtons
          )
        ), 

        openFilter

      )
    );
  }

});
